  margin: 0;\n  padding: 0; }\n\narticle, aside, canvas, figure, figure img, figcaption, hgroup,\nfooter, header, nav, section, audio, video {\n  display: block; }\n\ntable {\n  border-collapse: separate;\n  border-spacing: 0; }\n  table caption, table th, table td {\n    text-align: left;\n    vertical-align: middle; }\n\nli {\n  list-style: none; }\n\na {\n  text-decoration: none; }\n\na img {\n  border: 0; }\n\n:focus {\n  outline: 0; }\n\n* {\n  -webkit-box-sizing: border-box; }\n\ntextarea {\n  resize: none;\n  -webkit-appearance: none; }\n\ninput {\n  -webkit-appearance: none; }\n\nselect {\n  -webkit-appearance: none;\n  background-color: #fff; }\n\nhtml {\n  background-color: #fafafa;\n  color: #212121;\n  font-size: 100px;\n  -webkit-user-select: none;\n  user-select: none; }\n\nbody {\n  height: 100%;\n  width: 100%;\n  font-size: 14px;\n  color: #313131;\n  overflow-x: hidden;\n  line-height: 1;\n  background: url(\"../assets/images/loading.gif\") no-repeat center 250px; }\n\n.mt5 {\n  margin-top: 5px; }\n\n.mt10 {\n  margin-top: 10px; }\n\n.color80 {\n  color: #42b983; }\n\n.clear {\n  zoom: 1; }\n  .clear:after {\n    content: '';\n    display: table;\n    clear: both; }\n\n.display-flex, .user, .posts-list .content, .posts-list p, .author-info {\n  display: -moz-box;\n  /* Firefox */\n  display: -ms-flexbox;\n  /* IE10 */\n  display: -webkit-box;\n  /* Safari */\n  display: -webkit-flex;\n  /* Chrome, WebKit */\n  display: flexbox;\n  display: flex; }\n\n.flex1, .user .title, .user .posts-list h3, .posts-list .user h3, .user .about-info dt, .about-info .user dt, .user .topic-reply, .user .info {\n  -webkit-flex: 1;\n  -moz-box-flex: 1;\n  -ms-flex: 1;\n  -webkit-box-flex: 1;\n  box-flex: 1;\n  flex: 1; }\n\n.align-justify {\n  -moz-box-align: justify-content;\n  -ms-flex-align: align-content;\n  -ms-flex-item-align: justify-content;\n  -webkit-box-align: justify-content;\n  /*老语法*/\n  -webkit-align-items: justify-content;\n  /*新语法*/\n  align-content: j